import React from "react"
import Link from "gatsby-link"
import hobbits from "./images/race_hobbit.jpg"
import elves from "./images/race_woodelf.jpg"
import dwarves from "./images/race_dwarf.jpg"
import humans from "./images/race_human.jpg"
import beornings from "./images/race_beorning.jpg"
import urukhais from "./images/race_urukhai.jpg"
import orcs from "./images/race_orc.jpg"
import ologs from "./images/race_ologhai.jpg"
import lhuths from "./images/race_uruklhuth.jpg"
import harads from "./images/race_haradrim.jpg"
import Races from "../components/races"
import YellowButtonLG from "../components/Buttons/YellowButtonLG";
import RedButtonLG from '../components/Buttons/RedButtonLG';
import { navigateTo } from "gatsby-link"
import NewsPreview from "../components/NewsPreview";



export default ({ data }) => 
<div>
    <div className="jumbotron">
        <div className="container">
            <h1>Welcome to Return of the Shadow!</h1>
            <p>Return of the Shadow is a game based on the Fourth Age of Arda, J.R.R. Tolkien's wonderful world of fantasy. Game play takes place after the destruction of the One Ring. After Sauron's fall, a period of peace was laid about the land, and the Elves, Humans, Dwarves, and Hobbits prospered.</p>
                <YellowButtonLG path = "/getting-started">Getting Started &raquo;</YellowButtonLG> <RedButtonLG path="/connect">Start Playing &raquo;</RedButtonLG>
        </div>
    </div>
    <section className="news-section">
        <div className="container-fluid">
            <div className="row">
                <h3 className="news-title">LATEST NEWS &amp; UPDATES <Link to="/news">View all news &raquo;</Link></h3>
            </div>
            {data.allMarkdownRemark.edges.map(({ node }) =>
                <NewsPreview
                    key = {node.id}
                    title = {node.frontmatter.title}
                    date = {node.frontmatter.date}
                    path = {node.frontmatter.path}
                    desc = {node.frontmatter.desc}
                    author = {node.frontmatter.author}
                >
                {node.excerpt}
                </ NewsPreview>
            )}
        </div>
    </section>
    <div className="jumbotron races">
            <div className="container">
                <h1 className="text-right">Playable Races</h1>
                <p className="races-p">Whether as a stalwart defender of Vinyanost, or a fierce guardian of Dol-Guldur, deciding which race to play will define who you'll fight for in this neverending war.</p>
                <div className="race-btn">
                    <YellowButtonLG path = "/races">LEARN MORE</YellowButtonLG>
                </div>
            </div>
    </div>
    <section className="classes">
        <div className="container">
            <div className="row">
                <h1 className="title">Classes</h1>
                <div className="col-lg-6">
                    <h2 className="text-center">Warrior</h2>
                    <p>Warriors are very proficient in raw fighting skill, and are generally well
trained in their knowledge of the wilderness.  However, they are rather
mediocre in their talent as a cleric and only partially tuned to the spirit
world.  Their knowledge of magery is generally lacking.
</p>
                </div>
                <div className="col-lg-6">
                    <h2 className="text-center">Ranger</h2>
                    <p>
                    Rangers are the masters of stealth and wilderness: they are well trained
in the handling of animals, herblores and tracking.  Rangers do not tend
to be accustomed to the aggressive style of fighting that marks a seasoned
warrior; instead they depend on their nimble movements and quick reflexes
for a defensive style of combat.  The average ranger's understanding of
magical arts is only mediocre; but rangers, being attuned to natural
environs, are completely uncomfortable with clerical duties and the world
of wraiths.

                    </p>
                </div>
                <div className="col-lg-6">
                    <h2 className="text-center">Mystic</h2>
                    <p>
                    Mystics are rare in Arda; while most of Arda's inhabitants avoid wraiths
and their kin at all costs, these daring souls have heightened their
perception of the spiritual world in hopes of coming in contact with its
undead inhabitants.  However, the common mystic does not narrow its scope of
study to the clerical arts: most mystics are able to employ the arts of
magery with decent success.  But as a consequence of this rigorous mental
training, mystics are left with very little training as a fighter, and with
a dismal knowledge of wilderness the outdoors.

                    </p>
                </div>
                <div className="col-lg-6">
                    <h2 className="text-center">Mage</h2>
                    <p>
                    Mages are experts of those magical arts which emtail the manipulation of
the physical world: fire, lightning, ice and darkness.  In general, mages
make heavy use of their spells to cause as much physical harm to their
victim as possible.  However, the arts of magery also include control of
the physical body and scrying magics: teleportation and informative spells.
Mages generally receive a decent training in the aspects of the Wraith
world, and commonly find themselves knowledgeable enough in the arts of
animals and wilderness.  However, as mages spend most of their time
enthralled in intellectual pursuit, they neglect their physical bodies and
their ability to fight.

                    </p>
                </div>
            </div>
        </div>
    </section>
</div>


export const query = graphql `
query IndexQuery {
    allMarkdownRemark(
        limit: 4, 
        sort: {fields: [frontmatter___date], order: DESC},
        filter: {fileAbsolutePath: {regex: "/(news)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                frontmatter {
                    title
                    date(formatString: "MMMM DD, YYYY")
                    path
                    desc
                    author
                }
                excerpt(pruneLength: 280)
            }
        }
    }
}
`


